import { useDate } from "@/hooks/useDate"
import { useIndex } from "@/hooks/useIndex"

type Date = string

/**
 * GoalGoals
 * useGoal()Goal,useGoals()
 */
type Goal = {

    /**
     * indexGoalUNIX
     */
    index: number

    /**
     * Goal
     */
    title: string

    /**
     * Goal
     */
    description?: string
}

/**
 * GoalsGoal
 */
interface Goals {

    /**
     * Goal
     */
    goals: Goal[]

    /**
     * Goal
     */
    schedule: Schedule

    /**
     * goals
     * goals
     */
    currentIndex: number

    /**
     * currentIndex
     */
    maxIndex: number

    /**
     * Goals
     */
    createdDate: Date

    /**
     * []
     */
    targetDate: Date | null

    /**
     * Goals
     * currentIndexmaxIndexcompletetrue
     */
    compelete: boolean

    /**
     * GoalsUNIX
     */
    index: number
}

/**
 * GoalsGoal
 */
type Schedule = "daily" | "weekly" | "monthly"

/**
 * Goal
 * GoalGoals
 */
function useGoal({title, description = ''}: {
    title: string,
    description?: string
}): Goal {
    return {
        index: useIndex(),
        title: title,
        description: description,
    }
}

/**
 * Goal
 * useGoal()Goal
 */
function useGoals({ goalList, schedule = "daily", currentIndex = 0, maxIndex = goalList.length - 1, targetDate = null, compelete = false }: {
    goalList: Goal[],
    schedule?: Schedule, 
    currentIndex?: number
    maxIndex?: number,
    targetDate?: Date | null,
    compelete?: boolean
}): Goals {
    return {
        goals: goalList,
        schedule: schedule,
        currentIndex: currentIndex,
        maxIndex: maxIndex,
        createdDate: useDate(),
        targetDate: targetDate,
        compelete: compelete,
        index: useIndex()
    }
}

export type {
    Goal,
    Goals,
    Schedule,
    Date,
}

export {
    useGoal,
    useGoals,
}