import { useDate } from "@/hooks/useDate"
import { useIndex } from "@/hooks/useIndex"

type Title = string
type Subtitle = string
type Note = string
type Tags = string[]
type Step = {
    text: string,
    done: boolean
}
type Steps = Step[]
type Index = number
type Date = string
type Type = "task" | "goal"

/**
 * ItemTodoGoal
 * useItem(...)ItemuseItem
 */
interface Item {

    // Item
    title: Title,

    /**
     * 
     */
    subtitle: Subtitle,

    /**
     * noteItem
     */
    note: Note,

    /**
     * 
     * tagsItemtags
     */
    tags: Tags,

    /**
     * stepstextdone
     */
    steps: Steps,

    /**
     * Item
     * indexItemkey
     * ItemUNIX
     * storepush
     */
    index: Index,

    /**
     * Item
     * 
     * storepush
     */
    createdDate: Date,

    /**
     * Item
     * null
     * 
     * storepush
     */
    targetDate: Date | null,

    /**
     * []
     */
    type: Type,
}
type Items = Item[]

/**
 * moment().format('x')index
 */
const createIndex = (): number => useIndex()

/**
 * YYYY-MM-DD
 * @returns YYYY-MM-DD
 */
const createDate = (): string => useDate()

/**
 * Item
 * title,
 */
function useItem({title, subtitle = "", note = "", tags = [], steps = [], targetDate = null, type = "task"}: {
    title: string,
    subtitle?: string,
    note?: string,
    tags?: Tags,
    steps?: Steps,
    targetDate?: Date | null,
    type?: Type
}): Item {
    return {
        title: title,
        subtitle: subtitle,
        note: note,
        tags: tags,
        steps: steps,
        index: createIndex(),
        createdDate: createDate(),
        targetDate: targetDate,
        type: type,
    }
}
function useStep({text, done = false}: Step): Step {
    return {
        text: text,
        done: done
    }
}

export type {
    Item,
    Items,
    Title,
    Subtitle,
    Note,
    Tags,
    Step,
    Steps,
    Index,
    Date,
    Type,
}
export {
    useItem,
    useStep
}